import React from 'react'
import Common from './Common'


const RightSide = () => {
  
  return (
    <>
        <Common side="d-flex flex-row-reverse" 
                image="images/8be8de663c4ab7eee8b3e53c31cbb581.jpeg" 
                heading="Neue Toyota Concept"
                summary="At the 2016 Consumer Electronics Show (CES) in Las Vegas, Toyota is demoing its idea of the Future of Mobility. As has become plainly obvious over the last couple years, Toyota believes plug-in vehicles will fail and that fuel cell EVs are the future.While other companies are at CES showing off battery based EVs, Toyota is sticking with their fuel cells." 
                ID="RightSide"
                />
    </>
  )
}

export default RightSide
